<template>
  <div class="h-screen flex bg-gray-100">
    <!-- Languette de gauche -->
    <aside class="ml-4 mb-6 w-1/5 bg-white rounded-lg shadow-lg p-6">
      <h2 class="text-lg text-center font-bold mb-4">Vos seuils</h2>
      <div>
        <label for="temperature" class="block mb-2 font-medium text-gray-700">Seuil de température</label>
        <input id="temperature" type="range" min="-10" max="40" step="1" class="w-full cursor-pointer" v-model="greenhouse.targetTemperature"/>
        <p class="mt-2 text-gray-500">
          Valeur choisie :
          <span class="font-bold">{{ greenhouse.targetTemperature }}</span>°C
        </p>
      </div>
    </aside>

    <!-- Contenu principal -->
    <main class="flex-1 flex flex-col items-center p-6">
      <h1 class="text-2xl font-bold mb-6">Votre serre : {{ greenhouse.name }}</h1>
      <div class="w-full h-full bg-white shadow-lg rounded-lg flex items-center justify-center">
        <!-- Graphiques -->
        <p class="text-gray-500 text-lg">
          Température actuelle : <span class="font-bold">{{ greenhouse.currentTemperature }}</span>°C
        </p>
        <p class="text-gray-500 text-lg mt-4">
          Humidité actuelle : <span class="font-bold">{{ greenhouse.currentHumidity }}</span>%
        </p>
      </div>
    </main>
  </div>
</template>

<script>
//manque du post pour changer valeur du seuil
export default {
  name: "GreenhouseDetails",
  data() {
    return {
      greenhouse: {
        name: "",
        targetTemperature: 30,
        currentTemperature: null,
        currentHumidity: null,
      },
    };
  },
  mounted() {
    this.fetchGreenhouseData();
  },
  methods: {
    async fetchGreenhouseData() {
      try {
        const response = await fetch(
            `http://localhost:8080/api/greenhouses/${this.$route.params.id}`
        );
        const data = await response.json();
        this.greenhouse = {
          ...this.greenhouse,
          name: data.name,
          targetTemperature: data.targetTemperature,
          currentTemperature: data.temperatureSensor?.currentTemperature,
          currentHumidity: data.humiditySensor?.currentHumidity,
        };
      } catch (error) {
        console.error("Erreur lors du chargement des données de la serre :", error);
      }
    },
  },
};
</script>

